import React from 'react';
import { themeDefault } from '../../themeDefault';
import '../../scss/button-base.scss';

export interface ButtonProps {
  label: string;
  prop?: string;
  color?: string;
}

export const Button: React.FC<ButtonProps> = ({
  label,
  prop,
  color,
  ...props
}) => {
  let styling = {
    backgroundColor: (color!)? themeDefault.palette.primary.light: themeDefault.palette.primary.main,
    padding: themeDefault.spacing(1),
    margin: themeDefault.spacing(1),
  }
  let classNames = 'Pui-button-base';

  return ( 
    <button className={classNames} style={styling}>
      {label} {prop}
    </button> 
  );
}